import React from "react";
import styles from './SettingsButton.module.css';
import { useNavigate } from "react-router-dom";
import trash from '../../Utils/images/Trash_font_awesome.svg.png'

const SettingsButton: React.FC<Props> = ({name, routing, hadnleClick, handleRemove, thisPosition = false}) => {
    const navigate = useNavigate();
    function openPage () {
        if (!thisPosition) {
            navigate(routing)
        }
        hadnleClick()
    }

    function deleteSome () {
        handleRemove(routing)
    }

    return (
        <button className={styles.container} onClick={openPage}>
            <p className={styles.header}>{name}</p>
            <img src={trash} alt="" className={styles.image} onClick={(e) => {e.stopPropagation();deleteSome()}}/>
        </button>
    )
}

export default SettingsButton

type Props = {
    name: string;
    routing: string;
    hadnleClick: any;
    handleRemove: any;
    thisPosition?: boolean;
}