import React, { useEffect, useState } from "react";
import styles from './OrderPage.module.css';
import { useAppDispatch, useAppSelector } from "../../services/store";
import { decrement, deletePosFromOrder, getAllDishes, increment } from "../../services/Slices/OrdersSlice";
import { TLocalMenu, TMenu, Torder, Tposition } from "../../Utils/Types";
import MenuContainer from "../../components/MenuContainer/MenuContainer";
import { jsPDF } from 'jspdf';
import '../../Utils/fonts/Roboto-Regular-normal';
import logo from '../../Utils/images/logo.jpg'
import { useInput } from "../../Utils/hooks";

const OrderPage = () => {

    const dispatch = useAppDispatch();
    const incrementPosition = async (pos: Tposition) => {
        await dispatch(increment(pos))
    }

    const decrementPosition = async (pos: Tposition) => {
        await dispatch(decrement(pos))
    }

    const allDishes: Array<Tposition> = useAppSelector(state => state.OrdersSlice.currentDishes);

    const order = useAppSelector(state => state.OrdersSlice.currentOrder);
    const namePdf = useInput(`${order.name} ${order.date}`, { isEmpty: true })
    const extraTextPdf = useInput(null, {})

    const totalPrice = order?.dishes.reduce((acc: any, item: TMenu) => {
        return acc + item.menu.reduce((acc, menu) => {
            return acc + menu.positions.reduce((acc, pos) => {
                return acc + pos.price * pos.count
            }, 0)
        }, 0)
    }, 0);



    function createAndSavePdf() {
        var doc = new jsPDF();
        doc.setFont("Roboto-Regular");
        doc.setFontSize(20);
        var img = new Image();
        img.src = logo;
        if (extraTextPdf.value !== null && extraTextPdf.value !== "") {
            doc.addImage(img, "jpg", 20, 0, 50, 50);
            doc.setFontSize(12)
            doc.text(extraTextPdf.value, 80, 15, {maxWidth: 100})
        } else {
            doc.addImage(img, "jpg", 80, 0, 50, 50);
        }
        doc.text(" :", 85, 50);
        doc.setFontSize(9);
        for (let i = 0; i < allDishes.length; i++) {
            if (i === 50) {
                doc.text("1/2", 185, 290)
                doc.addPage()
            }
            if (i < 50) {
                doc.text(`${allDishes[i].name}    ${JSON.stringify(allDishes[i].price)} * ${JSON.stringify(allDishes[i].count)} = ${JSON.stringify(allDishes[i].price * allDishes[i].count)}`, 25, 60 + 4 * i)
            } else {
                doc.text(`${allDishes[i].name}    ${JSON.stringify(allDishes[i].price)} * ${JSON.stringify(allDishes[i].count)} = ${JSON.stringify(allDishes[i].price * allDishes[i].count)}`, 25, 15 + 4 * (i - 50))
            }
        }
        doc.setFontSize(20);
        if (allDishes.length > 49) {
            doc.text(`: ${JSON.stringify(totalPrice)}`, 120, 25 + 4 * (allDishes.length - 50));
        } else {
            doc.text(`: ${JSON.stringify(totalPrice)}`, 120, 70 + 4 * allDishes.length)
        }
        doc.setFontSize(12)
        doc.text("________", 10, 275);
        doc.text("________", 130, 275);
        if (allDishes.length > 49) {
            doc.setFontSize(9)
            doc.text("2/2", 185, 290)
        }
        doc.save(`${namePdf.value}.pdf`)
    }

    const removePositionFromOrder = (pos: Tposition) => {
        dispatch(deletePosFromOrder(pos))
    }

    return (
        order
            ? (<div className={styles.page}>
                <h1 className={styles.header}>: {order.name}</h1>
                <div className={styles.dishesContainer}>
                    {order.dishes && order.dishes.length > 0 && order.dishes.map((menu: TMenu) => {
                        return <MenuContainer menu={menu} removedPos={true} incrementPos={incrementPosition} decrementPos={decrementPosition} handleClick={incrementPosition} removePosition={removePositionFromOrder} />
                    })}
                    <div className={styles.descriptions}>
                        <div className={styles.descriptionContainer}>
                            <h3 className={styles.secondHeader}> 1</h3>
                            <p className={styles.description}>{order.description1}</p>
                        </div>
                        <div className={styles.descriptionContainer}>
                            <h3 className={styles.secondHeader}> 2</h3>
                            <p className={styles.description}>{order.description2}</p>
                        </div>
                    </div>
                </div>
                <div className={styles.info}>
                    <p className={styles.totalPrice}> : {totalPrice}</p>
                </div>
                <label>
                    <p className={styles.labelText}> -</p>
                    <input className={styles.input} placeholder=" " value={namePdf.value} onBlur={e => namePdf.onBlur(e)} onChange={e => namePdf.onChange(e)} />
                    {(namePdf.isDirty && namePdf.isEmpty) && <p className="errorText">    </p>}
                </label>
                <label>
                    <p className={styles.labelText}> </p>
                    <input className={styles.input} placeholder=" " value={extraTextPdf.value} onBlur={e => extraTextPdf.onBlur(e)} onChange={e => extraTextPdf.onChange(e)}/>
                </label>
                <button type="button" className={styles.button} disabled={!namePdf.inputValid} onClick={createAndSavePdf}> </button>
            </div>
            )
            : null
    )
}

export default OrderPage;